@import url(https://fonts.googleapis.com/css?family=Roboto&subset=latin-ext);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    padding: 0;
    margin: 0;
}

body {
 font-family: 'Roboto', sans-serif;
 padding-top: 50px;
 padding-bottom: 30px;
 background: #eef2ed;
}

button {
    display: block;
    border: none;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    font-size: 16px;
    text-transform: uppercase;
    padding: 5px 10px;
    margin: auto;
    margin-bottom: 40px;
    border-radius: 5px;
    background: #fff;
    transition: 0.5s;
    cursor: pointer;
    outline: none;
}

button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.user {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    max-width: 450px;
    margin: auto;
    margin-bottom: 35px;
    padding: 20px 15px;
    background: #fff;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    transition: .5s;
    cursor: pointer;
}

.user:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
} 

.picture {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding-right: 15px;
}

span {
    font-weight: 700;
}

h1 {
    font-size: 30px;
}
